.text {
    background: linear-gradient(to right,
            hsl(98 100% 62%),
            hsl(204 100% 59%));
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-align: center;
}

.text h1 {
    font-size: 25px;
    line-height: 1.1;
}